import React from "react";
import Link from 'next/link'
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from "reactstrap";
import FaPhone from "react-icons/lib/fa/phone";

export default class Example extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  render() {
    return (
      <div>
        <div className="text-right">
          <a className="phone" href="tel:07484675664">
            <FaPhone />07484675664
          </a>
        </div>
        <Navbar light expand="md">
          <NavbarBrand href="/">
            <img
              src="/static/logo.jpg"
              alt="Top london removals"
              className='logo'
            />
          </NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto navi" navbar>
              <NavItem>
                {/* <NavLink className="aa"> */}
                <Link href='/' passHref><a>HOME</a></Link>
                {/* </NavLink> */}
              </NavItem>
              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  REMOVALS
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem>
                    <NavLink>
                <Link href='/home-removals' passHref><a>HOME REMOVALS</a></Link>
                    </NavLink>
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>
                    <NavLink>
                <Link href='/office-removals' passHref><a>OFFICE REMOVALS</a></Link>
                    </NavLink>
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>
                    {/* <NavLink href="/international-removals"> */}
                <Link href='/international-removals' passHref><a>INTERNATIONAL REMOVALS</a></Link>
                    {/* </NavLink> */}
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
              <NavItem>
                {/* <NavLink href="/man-and-van">MAN AND VAN</NavLink> */}
                <Link href='/man-and-van' passHref><a>MAN AND VAN</a></Link>

              </NavItem>
              <NavItem>
                {/* <NavLink href="/prices">PRICES</NavLink> */}
                <Link href='/office-removals' passHref><a></a></Link>

              </NavItem>
              <NavItem>
                <NavLink href="/get-a-quote">GET A QUOTE</NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
}



